@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Marcellus&family=Playfair+Display:wght@700&family=Playpen+Sans:wght@300;400&family=Poppins:wght@300;400&display=swap');

.banner-img {
  margin-top: 1px;
  width: 100%;
  height: 600px;
}

.content{
  width: 100%;
  height: 405px;
  top: 90px;
  left: 0;
  position: absolute;
  background: rgba(0, 0, 0, 0.0);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transition: 0.6s;
  transform: translateZ(40px);
}

.content:hover{
  opacity: 1;
  transform: translateY(27px);
}

.content h1{
  font-size: 100px;
  color: black;
  -webkit-text-stroke: 2px white;
  margin-bottom: 15px;
}

.content p{
  font-size: 40px;
  -webkit-text-stroke: 1px chocolate;
  font-family: 'Playpen Sans', cursive;
  color: white;
}

.productsec{
  background-color: white;
  color: black;
}

.pheadline{
  text-align: justify;
  font-family: 'Playpen Sans', cursive;
  color: #555;
}
  
.home-page {
  display: flex;  
  flex-wrap: wrap;
}

.home-page .card {
  background-color: white;
  border-color: darkslategrey;
  width: 18rem;
}
.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
  
.home-page .card-price {
  color: green;
  font-weight: bold;
}

.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}
  
.home-page .card-text {
  color: rgb(0, 0, 0);
}
  
.home-page .card img:hover {
  transform: scale(0.9);
}
  
.home-page .card-img-top {
  height: 300px !important;
}

.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}
.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}
.filters{
  color: black !important;
  font-size: 30px;
  font-weight: bold;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.filters .head{
  color: black!important;
  font-family: 'Sedgwick Ave Display', cursive !important;
}
.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
  margin-bottom: 30px;
}
.filters button:hover{
  background-color: wheat !important;
  color: black !important;
}
.loadmore {
  color: green !important;
  font-weight: bold;
  font-size: 20px !important;
}
  
.homeproduct .bed{
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: space-between;
}

.homeproduct .bed .headline{
  margin-top: 160px;
  margin-left: 25px;
  font-family: 'Marcellus', serif;
  color: goldenrod;
  padding: 0;
}

.homeproduct .bed .headline h1{
  font-size: 100px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.homeproduct .bed .bedbanner{
  width: 50%;
  height: 500px;
  position: relative;
}

.homeproduct .bed .bedbanner img{
  width: 100%;
  height: 100%;
}

.top-bedbanner{
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: all 0.5s ease;
}

.top-bedbanner:hover{
  opacity: 1;
}

.doublead{
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 150px;
}

.chairbanner{
  width: 50%;
  height: 600px;
  position: relative;
}

.chairbanner img{
  width: 100%;
  height: 100%;
}

.top-chairbanner{
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: all 0.5s ease;
}

.top-chairbanner:hover{
  opacity: 1;
}

.chairbanner h1{
  font-size: 80px;
  margin-left: 150px;
  color: wheat;
  -webkit-text-stroke: 1px black;
  position: absolute;
  top: 83%;
  left: 10px;
  font-family: 'Marcellus', serif;
}

.chairbanner .btn{
  width: 100%;
  height: 50px;
  border-radius: 0;
  border: 1px solid black;
  background-color: wheat;
  color: darkslategray;
  font-size: 20px;
  font-weight: bold;
  font-family: 'Playpen Sans', cursive;
}

.chairbanner .btn:hover{
  background-color: black;
  color: goldenrod;
}

.tablebanner{
  width: 50%;
  height: 600px;
  position: relative;
}

.tablebanner img{
  width: 100%;
  height: 100%;
}

.top-tablebanner{
  position: absolute;
  right: 0;
  top: 0;
  opacity: 0;
  transition: all 0.5s ease;
}

.top-tablebanner:hover{
  opacity: 1;
}

.tablebanner h1{
  font-size: 80px;
  margin-left: 100px ;
  color: wheat;
  -webkit-text-stroke: 1px black;
  position: absolute;
  top: 83%;
  left: 10px;
  font-family: 'Marcellus', serif;
}

.tablebanner .btn{
  width: 100%;
  height: 50px;
  border-radius: 0;
  border: 1px solid black;
  background-color: wheat;
  color: darkslategray;
  font-size: 20px;
  font-weight: bold;
  font-family: 'Playpen Sans', cursive;
}

.tablebanner .btn:hover{
  background-color: black;
  color: #daa520;
}

.small-container{
  width: 100%;
  background-color: darkslategray;
  color: white;
  border: 1px solid black;
}

.col-5{
  flex-basis: 10%;
  margin-bottom: 80px;
  align-items: center;
  justify-content: space-evenly;
  justify-items: center;
  margin-left: 110px;
}

.col-5 .img{
  width: 100%;
  align-items: center;
  justify-content: center;
}

.title{
  text-align: center;
  margin: 0 auto 80px;
  position: relative;
  line-height: 60px;
  color: black;
  font-size: 50px;
  font-family: 'Marcellus', serif;
}