 @import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Marcellus&family=Playfair+Display:wght@700&family=Playpen+Sans:wght@300;400&family=Poppins:wght@300;400&family=Sedgwick+Ave+Display&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar{
  font-family: 'Poppins' , sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0px 8px 6px -6px grey;
}
.nav-link{
  font-weight: 300; 
  color: white;
}
.nav-link:hover{
  color: goldenrod;
}
.active{
  border-bottom: 0.1px solid white;
}
.navbar-brand{
  font-weight: 700;
  font-family: 'Marcellus', serif;
  letter-spacing: 0.3px;
}
.footer {
  color: white;
  padding: 20px;
  background: black; 
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 5px;
}
.wow{
  text-decoration: none;
  text-align: right !important;
}
.footer a:hover {
  color: goldenrod;
}

.footer .icons{
  text-align: right;
}
.icons .in:hover{
  color: goldenrod;
}
.head{
  font-weight: 500;
  text-align: center;
}
.wow{
  font-size: 22px;
  padding: 10px;
}

.pnf{
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pnf-title{
  font-size: 100;
  font-weight: 700;
}
.pnf-heading{
  font-weight: normal;
}
.pnf-btn{
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover{
  background-color: black;
  color: white;
}
.contact-wrapper{
  background-image: url(../public/images/wall.jpg);
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 30vh;
}
.contact-wrapper h1{
  text-align: center !important;
  font-family: 'Sedgwick Ave Display', cursive;
  font-size: 100px;
}
.map{
  width: 100%;
}
.contactus{
  margin: 0;
  padding: 0;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact-info{
  padding: 12px 10px;
  background-color: #D6AD60;
  color: black;
  border-radius: 10px;
  border: 1px solid black;
}
.contact-info h1{
  background: transparent;
  color: black;
}
.form .card{
  background-color: #D6AD60;
  border: 1px solid black;
}
.label{
  font-size: 18px;
}
.form button{
  padding: 12px 10px;
  font-size: 18px;
  border-radius: 10px;
  color: black;
  background-color: #CD6858;
  border: 1px solid black;
  transition: 0.5s;
}
.form button:hover{
  background: black;
  color: white;
  border: 1.5px solid burlywood;
}
.about-wrapper{
  background-image: url(../public/images/wall.jpg);
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 30vh;
}
.about-wrapper h1{
  text-align: center !important;
  font-family: 'Sedgwick Ave Display', cursive;
  font-size: 100px;
}
.aboutus{
  margin: 0;
  padding: 0;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  align-items: center;
}
.aboutus img{
  width: 100%;
  height: 70vh;
  padding: 10px;
  border-radius: 35%;
  border: 1px solid black;
}
.about-info{
  padding: 10px;
  background-color: #D6AD60;
  color: black;
  border-radius: 15%;
  font-size: 18px;
  border: 2px solid black;
}
.about-info h3{
  font-family: 'Sedgwick Ave Display', cursive !important;
  font-style: bold;
}
.testimonial h1{
  font-family: 'Poppins' , sans-serif;
}
.testimonial img{
  align-items: center;
  width: 10rem;
  padding: 10px;
  border-radius: 50%;
}
.testimonial p{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 17px;
  padding: 10px;
  display: inline-block;
  text-align: center;
  background-color: #CD6858;
  border-radius: 13%;
  border: 1px solid black;
  color: black;
}
.policy-wrapper{
  background-image: url(../public/images/wall.jpg);
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 30vh;
}
.policy-wrapper h1{
  text-align: center !important;
  font-family: 'Sedgwick Ave Display', cursive;
  font-size: 100px;
}
.cancel{
  width: 50%;
  height: 23vh;
  margin: 0;
  text-align: center;
  background-color: #D6AD60;
  color: black;
  border-radius: 10px;
  border: 1px solid black;
}
.cancel h1{
  font-size: 25px;
  font-family: 'Playfair Display', serif;
}
.cancel h4{
  padding-top: 5px;
  font-size: 17px;
  font-family: 'Poppins', sans-serif;
}
.return{
  width: 80%;
  height: 120vh;
  margin: 0;
  margin-bottom: 10px;
  background-color:  #CD6850;
  color: black;
  border-radius: 10px;
  border: 1px solid black;
  padding: 10px;
}
.return h1{
  text-align: center;
  font-size: 25px;
  font-family: 'Playfair Display', serif;
}
.return h2{
  text-align: center;
  padding-top: 5px;
  font-size: 17px;
  font-family: 'Poppins', sans-serif;
}
.return h3{
  font-size: 17px;
  font-family: 'Poppins', sans-serif;
  font-style: bold;
}
.return h4{
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
}

.product-link{
  text-decoration: none;
  color: black;
}
