
.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    flex-direction: column;
    border-radius: 0;
    background-image: url(../../public/images/contactbannercut.png);
  }

.form-container form{
    box-shadow: rgba(0,0,0,0) 0px 5px 15px;
    padding: 20px;
    border-radius: 10px;
    border: 1px solid black;
    background-color:  #D6AD60;
}

.form-container form h1{
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: 'Playfair Display', serif;
}

.form-container form input{
    border-bottom: 1px solid #000;
    border-radius: 10px;
}

.form-container form button{
    border: 1px solid black;
    border-radius: 50px;
    background-color:  #CD6858;
    color: black;
    width: 250px;
}

.form-container form button:hover{
    border: 1.5px solid burlywood;
    background-color: black;
    color: white;
}